}

/* Select2 */
.select2-container {
  width: 100% !important;
  border: 1px solid transparent;
  margin-top: 8px;
  transition: border-color 0.3s ease;
}
.select2-container:hover, .select2-container:focus {
  border-color: #000000;
}

.select2-container--default .select2-selection--single {
  height: 55.19px;
  border: 0;
  border-radius: 0;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  position: absolute;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 55.19px;
  padding-left: 15px;
  padding-right: 40px;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 13px;
  background-image: url('data:image/svg+xml,<svg width="20" height="13" viewBox="0 0 20 13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M2 2L10 10L18 2" fill="transparent" stroke="%23000000" stroke-width="3"/></svg>');
  background-repeat: no-repeat;
  transition: transform 0.3s ease;
}
.select2-container--default .select2-selection--single .select2-selection__arrow b {
  display: none;
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
  transform: translateY(-50%) scale(1, -1);
}

/* Select2 dropdown */
.select2-dropdown {
  border: 0;
  border-radius: 0;
}

.select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar {
  width: 5px;
}
.select2-container--default .select2-results > .select2-results__options::-webkit-scrollbar-thumb {
  background: #0c4552;
  border-radius: 10px;
}

.select2-results__option--selectable {
