.header {
  position: relative;
  z-index: 10;
  width: 100%;
  padding-top: 127px;
}
@media only screen and (max-width: 991px) {
  .header {
    padding-top: 30px;
  }
}
.header--opened .header__menu {
  transform: translateX(0);
}
.header--opened .header__burger span:first-child {
  opacity: 0;
}
.header--opened .header__burger span:last-child::before {
  transform: translateY(12px) rotate(45deg);
}
.header--opened .header__burger span:last-child::after {
  transform: translateY(-12px) rotate(-45deg);
}
.header + * {
  padding-top: 90px;
}
@media only screen and (max-width: 767px) {
  .header + * {
    padding-top: 40px;
  }
}
@media only screen and (max-width: 767px) {
  .header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.header__menu {
  margin-top: 60px;
}
@media only screen and (max-width: 991px) {
  .header__menu {
    margin-top: 30px;
  }
}
@media only screen and (max-width: 767px) {
  .header__menu {
    position: fixed;
    inset: 0;
    z-index: 20;
    margin-top: 0;
    transform: translateX(100%);
    transition: transform 0.3s ease;
  }
}
.header__burger {
  position: relative;
  z-index: 21;
  display: none;
  width: 40px;
  height: 40px;
  margin-right: 12%;
}
@media only screen and (max-width: 767px) {
  .header__burger {
    display: block;
  }
}
.header__burger span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  transition: 0.5s ease;
}
.header__burger span:first-child {
  background-color: #ffffff;
}
.header__burger span:last-child::before, .header__burger span:last-child::after {
  content: "";
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition: 0.5s ease-in-out;
}
.header__burger span:last-child::before {
  top: -12px;
}
.header__burger span:last-child::after {
  bottom: -12px;
}

