.awards__item {
  position: relative;
  height: 0;
  padding-top: 47.5%;
}
.awards__item::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100%;
  background-color: #0c4552;
  pointer-events: none;
}
.awards__item:nth-child(even)::before {
  right: 100%;
}
.awards__item:nth-child(odd)::before {
  left: 100%;
}
.awards__item img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top right;
}

.news-card {
  position: relative;
  display: flex;
  flex-direction: column;
  border-left: 7px solid #ffffff;
  padding: 0 24px;
}
.news-card::before {
  position: absolute;
  top: 0;
  left: -7px;
  content: "";
  width: 0;
  height: 100%;
  background-color: #0c4552;
  transition: 0.3s ease;
}
.news-card:hover::before {
  width: 7px;
}
.news-card:hover .news-card__title span {
  background-size: 100% 1px;
}
.news-card__title {
  font-size: 20px;
  line-height: 1.32;
}
.news-card__title span {
  background: no-repeat 0 100%;
  background-image: linear-gradient(currentColor, currentColor);
  background-size: 0 1px;
  transition: background-size 0.6s ease;
}
.news-card__text {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.25;
  margin-top: 10px;
  margin-bottom: 28px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.news-card__date {
  color: #8f8f8f;
  font-size: 16px;
  line-height: 1.25;
  margin-top: auto;
}

.news {
  position: relative;
  padding-bottom: 128px;
}
.news::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 40px;
  background-color: #ed344a;
}
.news__outer {
  position: relative;
  padding: 56px 0 86px;
  background-color: #575a64;
}
.news__container {
  max-width: 1040px;
}
.news__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px 0;
  margin-top: 45px;
}
@media only screen and (max-width: 991px) {
  .news__grid {
    grid-template-columns: repeat(1, 1fr);
  }
}
.news__more {
  max-width: 328px;
  width: 100%;
  margin-top: 30px;
  position: absolute;
  bottom: -22px;
  left: 50%;
  transform: translateX(-50%);
}

