.menu {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	gap: 125px;

	@include max(tl) {
		justify-content: space-between;
		gap: 30px;
	}

	@include max(ti) {
		overflow-y: auto;
		flex-direction: column;
		justify-content: center;
		background-color: $green-bg;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: 45px;
		padding: 0;
		list-style: none;

		@include max(ti) {
			flex-direction: column;
		}

		a {
			position: relative;
			font-family: $latoFont;
			font-size: 15px;
			line-height: 1.2;

			&::after {
				content: "";
				position: absolute;
				left: 0;
				bottom: -12px;
				display: block;
				width: 0;
				height: 2px;
				background-color: $green-18;
			}

			&:hover::after {
				animation: link-hover-line 0.3s ease forwards;
			}
		}
	}

	&__link {
		@include max(ti) {
			margin-top: 30px;
		}
	}
}